.dashboard--page {
  display: flex;
  /* flex-direction: column; */
  justify-content: center;
  align-items: center;

  width: 100%;
  height: 100vh;
}

.dashboard--section {
  height: 90%;
  width: 80%;

  background-color: white;

  border-radius: 20px;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px,
    rgba(0, 0, 0, 0.3) 0px 30px 60px -30px,
    rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
  padding: 2.5em;
}

/* Dashboard Title */
.dashboard--title--section {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.dashboard--title--section > * {
  margin: 0 20px;
}

.dashboard--title {
  font-size: 1.5em;
  font-weight: 600;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

/* Metrics */
.dashboard--mainmetrics--section {
  display: flex;
  justify-content: flex-start;

  margin: 2em 0;
}

.dashboard--mainmetrics--container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  height: 5em;
  width: 10em;

  margin: 0 2em;

  border-radius: 5px;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px,
    rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
}

.dashboard--mainmetrics--metric--number {
  font-size: 3em;
  font-weight: 700;
}
