.form {
  margin: 0 auto;
  width: 90%;

  border: 3px solid rgb(114, 36, 36);

  padding: 2em;

  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.section {
  border: 2px solid rgb(235, 220, 191);
  padding: 2em;
}

.question--container {
  width: 500px;

  display: flex;
  justify-content: space-between;
}

/*=====  Input  ======*/

.question--container > input {
  width: 300px;
}

input[type='number'] {
  width: 15em;
  height: 3em;
}

.array--input > input {
  width: 300px;
  height: 3em;
}

.value_and_unit > input[type='number'] {
  width: 150px;
}

.value_and_unit > select {
  width: 150px;
}

/*=====  Radio  ======*/

.boolean--radio {
  width: 300px;

  display: flex;
  justify-content: center;
  align-items: center;
}

.boolean--radio--content--container {
  margin-left: 10px;
}

.boolean--radio--content--container > input {
  width: 2em;
  height: 2em;
}

/*=====  Checkbox  ======*/
.checkbox--section {
  display: flex;
  flex-direction: column;

  width: 300px;
}

.checkbox--container {
  font-size: 1.25em;
  display: flex;
  justify-content: space-between;
}

.checkbox--container > input {
  width: 2em;
  height: 2em;
}

.checkbox--array--input {
  width: 10em !important;
}

.checkbox--array--section {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

/*=====  Select  ======*/

.form select {
  width: 300px;
  height: 3em;
}

/*=====  Purchases section  ======*/
.purchase-section {
  display: flex;
  flex-direction: block;
}

.purchase--input--section {
  display: flex;
}

.purchase--entry--section {
  padding: 1.5em;
  border: 2px solid;
  border-color: rgb(114, 36, 36);
}

/*=====  Home  ======*/
.home--section {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.submit {
  width: 92.5vw;
  height: 2.5em;

  background-color: rgb(114, 36, 36);
  color: white;
  font-size: 2em;

  margin: 0 auto;

  display: inline;
}

.home--actions--section {
  width: 70%;
  /* background-color: red; */

  display: flex;
  flex-direction: column;
  align-items: center;
}

.home--actions--links {
  display: flex;
  justify-content: center;
}

.home--actions--loadFromDetail > input {
  width: 200px;
}

/*=====  Purchase  ======*/
.purchase--entry--field {
  width: 15em;
  height: 3em;
}

.purchaseTitleFieldPair--section {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
